.app__aboutus {
    position: relative;
}

.app__aboutus-overlay {
    position: absolute;
    inset: 0;
}

.app__aboutus-overlay img {
    z-index: 0;
    width: 391px;
    height: 495px;
}

.app__aboutus-content {
    z-index: 2;
    width: 100%;
}

.app__aboutus-content_about {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    text-align: right;
    flex-direction: column;
}

.app__aboutus-content_history {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    text-align: left;
    flex-direction: column;
}

.app__aboutus-content_knife {
    margin: 2rem 4rem;
}

.app__aboutus-content_knife img {
    height: 910px;
}
.app__aboutus-content_about p,
.app__aboutus-content_history p{
    color: var(--color-gray);
    margin: 2rem 0;
}
@media screen and (max-width:2000px){
    .app__aboutus-content_knife img{
        height:1110px ;
    }
    .app__aboutus-content_about p,
.app__aboutus-content_history p{
    
    margin: 4rem 0;
}
}
@media screen and (max-width:900px) {
    .app__aboutus-content{
        flex-direction: column;
    }
    .app__aboutus-content_knife{
        margin: 4rem 0;
    }
}
@media screen and (max-width:650px){
    .app__aboutus-overlay img{
        width: 80%;
        height:320px;
    }
}