.app__menu {
    background-color: var(--color-black);
    flex-direction: column;
}

.app__menu-title {
    text-align: center;
    margin-bottom: 2rem;
}

.app__menu-menu {
    width: 100%;
    flex-direction: row;
    display: flex;
    margin: 2rem 0;
    justify-content: center;
    align-items: flex-start;
}

.app__menu-menu_wine,
.app__menu-menu_cocktails {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.app__menu-menu_heading {
    color: var(--color-white);
    font-family: var(--font-base);
    font-weight: 600;
    font-size: 45px;
    line-height: 58.5px;
    letter-spacing: 0.04em;
}
.app__menu-menu_img{
    width: 480px;
    margin: 0 2rem;
}
.app__menu-menu_img img{
    width: 100%;
    height: auto;
}
.app__menu-menu_items{
    display: flex;
    flex-direction: column;
}
@media screen and (max-width:2000px) {
    .app__menu-menu_img{
        width: 650px;
    }
    .app__menu-menu_img img{
        height: 920px;
    }
}
@media screen and (max-width:1150px){
    .app__menu-menu{
        flex-direction: column;
        align-items: center;
        width: 100%;
    }
    .app__menu-menu_img{
        margin: 3rem 0;
    }
}
@media screen and (max-width:650px) {
    .app__menu-menu_img{
        width: 100%;
    }
    .app__menu-menu_heading{
        line-height: 48.5px;
        font-size: 35px;
    }
}