.app__laurels {
    background-color: var(--color-black);
    display: flex;
    flex-direction: row;
    width: 100%;

}

.app__laurels-content {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 4rem;
}

.app__laurels-content_single {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.app__laurels-content_main {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    margin-bottom: 6rem;
}

.app__laurels-content_image {
    width: 180px;
    height: 120px;
    margin-right: 2rem;
}

.app__laurels-content_image img {
    width: 100%;
    height: 100%;
}

.app__laurels-content_p {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-right: 1rem;
}

.app__laurels-p_1 {
    color: var(--color-golden);
    font-size: 30px;
    font-weight: 400;
    font-family: var(--font-base);
    letter-spacing: 0.04em;
    line-height: 22px;
    margin-bottom: 1rem;
}

.app__laurels-p_2 {
    color: #95afc0;
    font-size: 18px;
    font-weight: 300;
    letter-spacing: 0.04em;
    line-height: 25px;
}

.app__laurels-image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width:1500px) {
    .app__laurels {
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    .app__laurels-image img{
        width: 600px;
        height: 600px;
        margin-left: 12rem;
        
    }
}
@media screen and (max-width:1050px){
    .app__laurels-image img{
        width: 400px;
        height: 400px;
        margin-left: 5rem;
    }
    .app__laurels-content_image{
        width: 150px;
        height: 100px;
    }
}
@media screen and (max-width:750px) {
    .app__laurels-content_single{
        display: flex;
        flex-direction: column;
    }
    .app__laurels-content_main{
        display: flex;
        flex-direction: column;
        margin-top: 0;
        margin-bottom: 2rem;
    }
    .app__laurels-content_image{
        width: 100px;
        height: 80px;
        margin-bottom: 2rem;
    }
    .app__laurels-content_image img{
        margin-left: 7rem;
    }
    .app__laurels-p_1 {
        font-size: 20px;
        font-weight: bolder;
        display: flex;
        justify-content: center;
        margin-left: 6rem;
        text-align: center;
        align-items: center;
    }
    .app__laurels-p_2{
        font-size: 15px;
    }
    .app__laurels-image{
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        width: 300px;
        height: 200px;
         margin-right: 0;
        
    }
    .app__laurels-image img{
        width: 100%;
        height: 100%;
        object-position: center;
        margin-left: 1rem;
    }

}